import React, { useState } from "react";
import axios from "axios";
// nerilen balang stateleri
const initialMessage = "";
const initialEmail = "";
const initialSteps = 0;
const initialIndex = 4; //  "B" nin bulunduu indexi

export default function AppFunctional(props) {
  const [index, setIndex] = useState(initialIndex);
  const [message, setMessage] = useState(initialMessage);
  const [email, setEmail] = useState(initialEmail);
  const [steps, setSteps] = useState(initialSteps);
  // AAIDAK HELPERLAR SADECE NERDR.
  // Bunlar silip kendi mantnzla sfrdan gelitirebilirsiniz.

  function getXY() {
    const coordinates = [(index % 3) + 1, Math.floor(index / 3) + 1];
    return coordinates;

    // Koordinatlar izlemek iin bir state e sahip olmak gerekli deildir.
    // Bunlar hesaplayabilmek iin "B" nin hangi indexte olduunu bilmek yeterlidir.
  }

  function getXYMesaj() {
    // Kullanc iin "Koordinatlar (2, 2)" mesajn izlemek iin bir state'in olmas gerekli deildir.
    // Koordinatlar almak iin yukardaki "getXY" helpern ve ardndan "getXYMesaj" kullanabilirsiniz.
    // tamamen oluturulmu stringi dndrr.
    return `Koordinatlar (${getXY()[0]}, ${getXY()[1]})`;
  }

  function reset() {
    // Tm stateleri balang deerlerine sfrlamak iin bu helper kullann.
    console.log("reset");
    setIndex(initialIndex);
    setMessage(initialMessage);
    setEmail(initialEmail);
    setSteps(initialSteps);
  }

  function sonrakiIndex(targetIndex) {
    setIndex(targetIndex);
    setSteps(steps + 1);
    setMessage(initialMessage);
  }

  function ilerle(evt) {
    const yon = evt.target.id;
    console.log("ilerle", yon);
    // Bu event handler, "B" iin yeni bir dizin elde etmek zere yukardaki yardmcy kullanabilir,
    switch (yon) {
      case "left":
        if (index % 3 === 0) {
          setMessage("Sola gidemezsiniz");
        } else {
          sonrakiIndex(index - 1);
        }
        break;
      case "up":
        if (index < 3) {
          setMessage("Yukarya gidemezsiniz");
        } else {
          sonrakiIndex(index - 3);
        }
        break;
      case "right":
        if (index % 3 === 2) {
          setMessage("Saa gidemezsiniz");
        } else {
          sonrakiIndex(index + 1);
        }
        break;
      case "down":
        if (index > 5) {
          setMessage("Aaya gidemezsiniz");
        } else {
          sonrakiIndex(index + 3);
        }
        break;
      default:
        break;
      // ve buna gre state i deitirir.
    }
  }

  function onChangeHandler(evt) {
    // inputun deerini gncellemek iin bunu kullanabilirsiniz
    setEmail(evt.target.value);
  }

  function onSubmitHandler(evt) {
    evt.preventDefault();
    // payloadu POST etmek iin bir submit handlera da ihtiyacnz var.
    console.log("submit");

    axios
      .post("http://localhost:9000/api/result", {
        x: getXY()[0],
        y: getXY()[1],
        steps: steps,
        email: email,
      })
      .then(function (response) {
        console.log(response);
        setMessage(response.data.message);
      })
      .catch(function (error) {
        console.log(error);
        setMessage(error.response.data.message);
      });

    setEmail(initialEmail);
  }

  return (
    <div id="wrapper" className={props.className}>
      <div className="info">
        <h3 id="coordinates">{getXYMesaj()}</h3>
        <h3 id="steps">{steps} kere ilerlediniz</h3>
      </div>
      <div id="grid">
        {[0, 1, 2, 3, 4, 5, 6, 7, 8].map((idx) => (
          <div key={idx} className={`square${idx === index ? " active" : ""}`}>
            {idx === index ? "B" : null}
          </div>
        ))}
      </div>
      <div className="info">
        <h3 id="message">{message}</h3>
      </div>
      <div id="keypad">
        <button onClick={ilerle} id="left">
          SOL
        </button>
        <button onClick={ilerle} id="up">
          YUKARI
        </button>
        <button onClick={ilerle} id="right">
          SA
        </button>
        <button onClick={ilerle} id="down">
          AAI
        </button>
        <button onClick={reset} id="reset">
          reset
        </button>
      </div>
      <form onSubmit={onSubmitHandler}>
        <input
          value={email}
          id="email"
          type="email"
          placeholder="email girin"
          onChange={onChangeHandler}
        ></input>
        <input id="submit" type="submit"></input>
      </form>
    </div>
  );
}
